<template>
    <div>
        <Nav></Nav>

<section class="section" id="about">
        <div class="container">
            <div class="row">
                <div class="right-text col-lg-5 col-md-12 col-sm-12 mobile-top-fix" >
                    <div class="left-heading">
                        <h5 >Info Kampus</h5>
                      
                    </div>
                    <div class="left-text">
                        <p>Ketahuilah informasi umum mengenai kampus favorit anda</p>
                    </div>
                </div> 
                <div class="col-lg-7 col-md-12 col-sm-12" data-scroll-reveal="enter left move 30px over 0.6s after 0.4s">
                    <img src="../assets/img/Kampus.jpg" class="rounded img-fluid d-block mx-auto" width="450px" alt="App">
                </div>               
            </div>
            <div class="row">
                <div class="col-lg-12">
                    <div class="hr"></div>
                </div>
            </div>
        </div>
    </section>

    <!-- Page Content -->
    <div class="container mb-5 my-5">
      <div class="row">


    <!-- Team Member 1 -->
    <div class="col-xl-3 col-md-6 mb-4" v-for="item in cont_kampus" v-bind:key="item.key">
      <button @click="Detail(item)" type="button" class="btn btn-primary card border-0 shadow" data-toggle="modal" data-target="#myModal">
        <img :src="item.logo" class="card-img-top" alt="...">
        <div class="card-body text-center">
          <h5 class="card-title mb-2">{{ item.nama }}</h5>
          <div class="card-text text-black-50">{{ item.lokasi }}</div>
        </div>
      </button>
    </div>
     </div>
    </div>
       
        <Footer></Footer>
    </div>
</template>

<script>
import Nav from './Nav'
import Kampus from './Kampus'
import Infografis from './Infografis'
import Footer from './Footer'
import firebase from 'firebase'

var Tawk_API=Tawk_API||{}, Tawk_LoadStart=new Date();
(function(){
var s1=document.createElement("script"),s0=document.getElementsByTagName("script")[0];
s1.async=true;
s1.src='https://embed.tawk.to/5dc42b331936a27a715fc5ce/default';
s1.charset='UTF-8';
s1.setAttribute('crossorigin','*');
s0.parentNode.insertBefore(s1,s0);
})();

export default {
    components:{
        Nav,
        Footer,
        Kampus,
        Infografis
    },
    // data: () =>({
    //   InfoJSON: [{
    //     img: '',
    //     title: 'Universitas Sumatera Utara',
    //     desc: 'Sumatera Utara'
    //   }
    //   ],
    //    TeamsJSON: [{
    //     img: '',
    //     title: 'Info Kampus',
    //     desc: 'Ketahuilah informasi umum mengenai kampus favorit anda'

    //     }]
    // })

      data (){
        return {
          cont_kampus: [],
          ref: firebase.firestore().collection('cont_kampus')
        }
      },

      created(){
        this.ref.onSnapshot((querySnapshot) => {
          this.cont_kampus=[];
          querySnapshot.forEach((doc) =>{
            this.cont_kampus.push({
              key: doc.id,
              nama: doc.data().nama_kampus,
              lokasi: doc.data().lokasi_kampus,
              logo: doc.data().logo_kampus
            })
          })
        })
      },

      methods: {
        Detail(item){
          this.$router.push({ name:'unidetail', params: {id:item.key}})
        }
      }
}
</script>

<style scoped>


#about {
  padding: 80px 0px;
  border-bottom: 1px solid #eee;
  padding-top: 8em;
}

@media (max-width: 765px) {
  #about {
    padding: 50px 0px 80px 0px;
        padding-top:6em;

  }
}

#about .right-text {
  padding-top: 20px;
  align-self: center;
  text-align: left;
}

#about .right-text p {
  margin-bottom: 40px;
}

#about h5
{
  font-size: 26px;
  font-weight: 700;
  color: #1e1e1e;
