  margin-bottom: 25px;
  letter-spacing: 0.5px;
}

#about p {
  margin-bottom: 30px;
  font-size: 16px;
  line-height: 30px;
  letter-spacing: 0.5px;
}


.card-title{
  color: black !important;    
}

.modal-header {
  text-align: left;
}

.head {
  background: linear-gradient(to right, #610969, #e30e8e);
