<template>
  <div>
      <Nav></Nav>

      <section class="section" id="about">

            <div class="container">
            <div class="row">
              <div class="col-lg-4 center mb-4">
                <img :src="item.logo_kampus" alt="Logo" width="200px">
              </div>
              <div class="col-lg-4">
                  <h4><b>Motto</b></h4>
                  <p>{{item.motto_kampus}}</p>
              </div>
              <div class="col-lg-4">
                  <h4><b>Visi</b></h4>
                  <p>{{item.visi_kampus}}</p>
              </div>
            </div>
                  </div>
                <hr>
<div class="left-text container">
            <p>Berdiri : {{item.tahun_kampus}}</p>
            <p>Lokasi : {{item.detaillokasi_kampus}}</p>
            <p>Website : <i>{{item.website_kampus}}</i></p>
            <p>Ranking : {{item.ranking_kampus}}</p>
</div>
<hr>
<div class="container">
  <p><b>Daftar Fakultas</b></p>
 <ul class="list-group left-text">
  <li v-for="faculty in fakultas" v-bind:key="faculty.key" class="list-group-item">{{faculty.nama}}</li>
</ul> 
</div>
      </section>
      <Footer></Footer>
  </div>
</template>

<script>
import Nav from './Nav'
import firebase from 'firebase'
import Footer from './Footer'

export default {
    components:{
        Nav,
        Footer
    },
    name:'unidetail',
      data (){
        return {
          item: {},
          fakultas: [],
          ref: firebase.firestore().collection('cont_kampus').doc(this.$route.params.id)
          
        }
      },

      created(){
         // const ref = firebase.firestore().collection('cont_kampus').doc(this.$route.params.id);

          this.ref.get().then((doc) =>{
              
                  this.key = doc.id;
                  this.item = doc.data()
              
          });
          this.ref.collection('cont_kampus_fak').onSnapshot((querySnapshot) => {
          this.fakultas=[];
          querySnapshot.forEach((doc) =>{
            this.fakultas.push({
              key: doc.id,
              nama: doc.data().fak_kampus
            })
          })
        })
      }

}
</script>

<style>

.left-text{
  text-align: justify;
}

#about {
  padding: 80px 0px;
  border-bottom: 1px solid #eee;
  padding-top: 9em;
  padding-left: 5em;
  padding-right: 5em;
}

@media (max-width: 765px) {
  #about {
    padding: 50px 0px 80px 0px;
        padding-top:6em;

  }
}

#about .right-text {
  padding-top: 20px;
  align-self: center;
  text-align: left;
}

#about .right-text p {
  margin-bottom: 40px;
}

#about h5
{
  font-size: 26px;
  font-weight: 700;
  color: #1e1e1e;
  line-height: 40px;
  margin-bottom: 25px;
  letter-spacing: 0.5px;
}

#about p {
  margin-bottom: 30px;
  font-size: 16px;
  line-height: 30px;
  letter-spacing: 0.5px;
}


</style>