  <header class="pricehead">
    <div class="container">
      <div class="intro-text">
        <div class="intro-lead-in">Question'Answer</div>
        <div class="intro-heading text-uppercase">Yok tanya tanya yok disini monggo</div>
      </div>
    </div>
  </header>

  <QnA></QnA>

<div class="container">
        <div class="panel panel-default widget">
            <div class="panel-heading">
                <span class="glyphicon glyphicon-comment"></span>
                <h3 class="panel-title">
                    Daftar Pertanyaan</h3>

            </div>
            <div class="panel-body">
                <ul class="list-group ">
                    <li class="list-group-item">
                        <div class="row mb-4" v-for="item in cont_comment" v-bind:key="item.key" >
                            <div class="col-xs col-md-11">
                                <div class="pad">
                                <div>
                                    <a href="http://www.jquery2dotnet.com/2013/10/google-style-login-page-desing-usign.html">
                                        {{ item.email }}</a>
                               <!--     <div class="mic-info">
                                        By: <a href="#">Bhaumik Patel</a> on 2 Aug 2013
                                    </div> -->
                                </div>
                                <div class="comment-text">
                                    <p>{{ item.komen }}</p>
                                </div>
                                <div class="action">
                                    <button style="font-size:10px " type="button" id="toggle" @click="balas" class="btn btn-primary btn-xs" title="Edit"> 
                                      Balas
                                    </button>
                                    <div id="balas">
                                      <input type="text" id="content" placeholder="balasan">
                                    </div>
                                    <!-- <button type="button" class="btn btn-success btn-xs" title="Approved">
                                        <span class="glyphicon glyphicon-ok"></span>
                                    </button> -->
                                </div>
                                </div>
                            </div>
                        </div>
                    </li>
                </ul>
                <a href="#" class="btn btn-primary btn-sm btn-block" role="button"><span class="glyphicon glyphicon-refresh"></span> Selengkapnya</a>
            </div>
        </div>
    
</div>

    <Footer></Footer>
    </div>
</template>

<script>
import Nav from './Nav'
import Footer from './Footer'
import QnA from './QnA'
import firebase from 'firebase'

var Tawk_API=Tawk_API||{}, Tawk_LoadStart=new Date();
(function(){
var s1=document.createElement("script"),s0=document.getElementsByTagName("script")[0];
s1.async=true;
s1.src='https://embed.tawk.to/5dc42b331936a27a715fc5ce/default';
s1.charset='UTF-8';
s1.setAttribute('crossorigin','*');
s0.parentNode.insertBefore(s1,s0);
})();



export default {
name: 'qna',
  components: {
    Nav,
    Footer,
    QnA
    },
//     data: () => ({
//     QnAJSON: [{
//         user:'Azka Dzaki',
//         question: 'Gimana ya'
//     }
//     ]
// }),
 
       data (){
        return {
          cont_comment: [],
          email: firebase.auth().currentUser.email,
          ref: firebase.firestore().collection('cont_comment')
        }
      },

      created(){
        this.ref.onSnapshot((querySnapshot) => {
          this.cont_comment=[];
          querySnapshot.forEach((doc) =>{
            this.cont_comment.push({
              key: doc.id,
              komen: doc.data().comment,
              email: doc.data().email
            })
          })
        })
      },

    methods: {
      balas: function(){
        var toggle  = document.getElementById("toggle");
        var content = document.getElementById("content");

        toggle.addEventListener("click", function() {
          content.style.display = (content.dataset.toggled ^= 1) ? "block" : "none";
        });

      // onSubmit(evt){
      // evt.preventDefault()
      // this.transactionRef.add(this.komentar)
      // alert('berhasil')
      //  }
               }
    }    
}
</script>

