<template>
<div>
<div class="">
<!-- Material form login -->
  <h5 class="card-header info-color white-text text-center py-4">
    <strong>Buat akun baru disini!</strong>
  </h5>
  <input type="text" v-model="email" placeholder="Email"><br>
  <input type="password" v-model="password" placeholder="Password"><br>
  <button class="btn btn-info" @click="signup">Daftar</button>
  <p>Sudah punya akun ? <router-link to="/login"> masuk</router-link></p>
  <p><router-link to="/">Halaman Utama</router-link></p>
</div>
      </div>
    <!--/.Content-->

</template>

<script>
import firebase from 'firebase'

export default {
    name: 'signup',
    data(){
        return{
            email: '',
            password: ''
        }
    },
    methods: {
        signup: function() {
            firebase.auth().createUserWithEmailAndPassword(this.email, this.password).then(
                function() {
                    alert('Akun berhasil dibuat')
                },
                function(err) {
                    alert('oops' + err.message)
                }
            )
        }
    }
}
</script>

<style scoped>
 .center{
    text-align: right;
  }

input{
  margin: 10px 0;
  width: 20%;
  padding: 15px;
}

button{
  margin-top: 20px;
  width: 10%;
  cursor: pointer;
}

p{
  margin-top: 40px;
  font-size: 13px;
}

p a{
  text-decoration: underline;
  cursor: pointer;
}
</style>