<template>
  <div>
      <Nav></Nav>

    <section class="section" id="about">

        <div class="container">
            <h4><b>{{item.judul}}</b></h4>
            <p>{{item.desc}}</p>
        </div>
        <hr>
        <div class="container left-text" v-for="post in isi" v-bind:key="post.key">
            <p><b>{{post.subjudul}}</b></p>
            <p>{{post.caption}}</p>
        </div>


      </section>
      <Footer></Footer>
  </div>
</template>

<script>
import firebase from 'firebase'
import Nav from './Nav'
import Footer from './Footer'

export default {
    components:{
        Nav,
        Footer
    },
    name:'infodetail',
      data (){
        return {
          item: {},
          isi: [],
          ref: firebase.firestore().collection('cont_info').doc(this.$route.params.id)
          
        }
      },

      created(){
         // const ref = firebase.firestore().collection('cont_kampus').doc(this.$route.params.id);

          this.ref.get().then((doc) =>{
              
                  this.key = doc.id;
                  this.item = doc.data()
              
          });
          this.ref.collection('cont_info_isi').onSnapshot((querySnapshot) => {
          this.isi=[];
          querySnapshot.forEach((doc) =>{
            this.isi.push({
              key: doc.id,
              subjudul: doc.data().subjudul,
              caption: doc.data().caption
            })
          })
        })
      }
}
</script>

<style>

.left-text{
  text-align: left justify;
}

#about {
  padding: 80px 0px;
  border-bottom: 1px solid #eee;
  padding-top: 9em;
  padding-left: 5em;
  padding-right: 5em;
}

@media (max-width: 765px) {
  #about {
    padding: 50px 0px 80px 0px;
        padding-top:6em;

  }
}

#about .right-text {
  padding-top: 20px;
  align-self: center;
  text-align: left;
}

#about .right-text p {
  margin-bottom: 40px;
}

#about h5
{
  font-size: 26px;
  font-weight: 700;
  color: #1e1e1e;
  line-height: 40px;
  margin-bottom: 25px;
  letter-spacing: 0.5px;
}

#about p {
  margin-bottom: 30px;
  font-size: 16px;
  line-height: 30px;
  letter-spacing: 0.5px;
}

</style>