<template>
    <div>
        <Nav></Nav>

  <header class="pricehead">
    <div class="container">
      <div class="intro-text">
        <div class="intro-lead-in">Informasi & Tips</div>
        <div class="intro-heading text-uppercase">Berbagai informasi menarik dan asik untuk menambah pengetahuan</div>
      </div>
    </div>
  </header>

  <div class="container head mb-4" id="caption">
    <h1 class="font-weight-light text">Informasi & Tips</h1>
  </div>

<!-- Page Content -->
<div class="container mb-5">
  <div class="row">


    <!-- Team Member 1 -->
    <div class="col-xl-3 col-md-6 mb-4" v-for="post in cont_info" v-bind:key="post.key">
      <button @click="Detail(post)" type="button" class="btn btn-primary card border-0 shadow" data-toggle="modal" data-target="#myModal">
        <img :src="post.poster" class="card-img-top" alt="...">
        <div class="card-body text-center">
          <h5 class="card-title mb-2">{{post.judul}}</h5>
          <div class="card-text text-black-50">{{post.desc}}</div>
        </div>
      </button>
    </div>


  </div>
</div>
        
        <Footer></Footer>
    </div>
</template>

<script>
import Nav from './Nav'
import Infografis from './Infografis'
import Footer from './Footer'
import firebase from 'firebase'

export default {
    components:{
        Nav,
        Footer,
        Infografis
    },

      data (){
        return {
          cont_info: [],
          ref: firebase.firestore().collection('cont_info')
        }
      },

      created(){
        this.ref.onSnapshot((querySnapshot) => {
          this.cont_info=[];
          querySnapshot.forEach((doc) =>{
            this.cont_info.push({
              key: doc.id,
              judul: doc.data().judul,
              desc: doc.data().desc,
              poster: doc.data().poster
            })
          })
        })
      },

      methods: {
        Detail(post){
          this.$router.push({ name:'infodetail', params: {id:post.key}})
        }
      }
    // data: () =>({
    //   InfoJSON: [{
    //     img: '',
    //     title: 'Tips Menjadi Mahasiswa Berprestasi',
    //     desc: 'Tips'
    //   }
    //   ]
    // })
}
</script>

