<template>
<div>
<div class="">
<!-- Material form login -->
  <h5 class="card-header info-color white-text text-center py-4">
    <strong>Masuk</strong>
  </h5>
  <input type="text" v-model="email" placeholder="Email"><br>
  <input type="password" v-model="password" placeholder="Password"><br>
  <button class="btn btn-info" @click="login"><router-link to="/" class="text-color">Masuk</router-link></button>
  <p>Belum punya akun ? <router-link to="/signup"> daftar</router-link></p>
  <p><router-link to="/">Halaman Utama</router-link></p>
</div>
      </div>
    <!--/.Content-->

</template>

<script>
import firebase from 'firebase'
import Nav from './Nav'
import Footer from './Footer'

export default {
  name: 'login',

  data(){
    return {
      email: '',
      password: ''
    };
  },
  methods: {
    login: function(){
      firebase.auth().signInWithEmailAndPassword(this.email, this.password).then(
        function() {
          alert('Berhasil masuk')
          this.$router.push('/about')
        },
        function(err){
          alert('ninuninu,' + err.message)
        }
      );
    }
  }
}
</script>

<style scoped>
.text-color{
  color: white;
}

  .login{
    margin-top: 8em;
  }

  .center{
    text-align: right;
  }

input{
  margin: 10px 0;
  width: 20%;
  padding: 15px;
}

button{
  margin-top: 20px;
  width: 10%;
  cursor: pointer;
}

p{
  margin-top: 40px;
  font-size: 13px;
}

p a{
  text-decoration: underline;
  cursor: pointer;
}
</style>