import Homepage from './components/Homepage';
import PricingPage from './components/PricingPage';
import qnaPage from './components/qnaPage';
import AboutPage from './components/AboutPage';
import TipsPage from './components/TipsPage';
import InJurusanPage from './components/InJurusanPage';
import InUniPage from './components/InUniPage';
import Login from './components/LoginPage';
import SimToefl from './components/SimToefl';
import SignUp from './components/SignUp';
import UniDetail from './components/UniDetail';
import TipsDetail from './components/TipsDetail';
import JurusanDetail from './components/JurusanDetail';
import Daftar from './components/Daftar';
import DaftarPage from './components/DaftarPage';
import SimA1 from './components/SimA1';
import SimJLPT from './components/SimJLPT';
import QnA from './components/QnA';


export default[
    { path: '/qnaa', component: QnA},

    { path: '/', component: Homepage},
    { path: '/pricing', component: PricingPage},
    { path: '/qna', name: 'qna', component: qnaPage},
    { path: '/about', component: AboutPage},
    { path: '/Jurusan', component: InJurusanPage},
    { path: '/Tips', component: TipsPage},
    { path: '/Jurusan', component: InJurusanPage},
    { path: '/Universitas', component: InUniPage},
    { path: '/simToefl', component: SimToefl},
    { path: '/Login', component: Login},
    { path: '*', redirect: Homepage},
    { path: '/signup', component: SignUp},
    { path: '/daftar', component: Daftar},
    { path: '/daftarpage', component: DaftarPage,meta:{requiresAuth: true}},
    { path: '/UniDetail/:id', name:'unidetail',component: UniDetail},
    { path: '/TipsDetail/:id', name:'infodetail', component: TipsDetail},
    { path: 'JurusanDetail/:id', name:'jurusandetail', component: JurusanDetail},
    { path: '/simA1', component: SimA1},
    { path: '/simJLPT', component: SimJLPT}



];

