<script>
import firebase from 'firebase'
import Nav from './Nav'
import Footer from './Footer'

export default {
    components: {
        Nav,
        Footer
    },
  data () {
    return {
      transactionRef: firebase.firestore().collection('cont_diri'),
      key: '',
      email: firebase.auth().currentUser.email,
      subject: 'lalala',
      data_diri:{
          alamat_diri: '',
          gender_diri: '',
          nama_diri: '',
          test_diri: '',
          email_diri: firebase.auth().currentUser.email,
      },
    }
  },

    methods: {
    onSubmit(evt){
      evt.preventDefault()
      this.transactionRef.add(this.data_diri)
      alert('berhasil')
      this.$router.push('/')

