<template>
  <div>
      <Nav></Nav>

    <section class="section" id="about">
            <div class="container">
            <div class="row">
              <div class="col-lg-6 center">
                  <h3><b>{{item.jurusan}}</b></h3>
                  <p>{{item.fakultas}}</p>
              </div>
              <div class="col-lg-6 left-text">
                  <p>{{item.deskripsi}}</p>
              </div>
                  </div>
                <hr>
                <h4 class="left-text mb-4">Kemampuan yang sesuai untuk jurusan {{item.jurusan}}?</h4>
                <div class="left-text"  v-for="post in isi" v-bind:key="post.key">
                    <p class="mb-2">- {{post.keahlian}}</p>
                </div>
                <hr>
                <div class="left-text">
                    <p>{{item.karir}}</p>
                </div>
            </div>
    </section>
      <Footer></Footer>
  </div>
</template>

<script>
import Nav from './Nav'
import Footer from './Footer'
import firebase from 'firebase'

export default {
    name: 'jurusandetail',
    components: {
        Nav,
        Footer
    },
    data (){
        return {
          item: {},
          isi: [],
          ref: firebase.firestore().collection('cont_jurusan').doc(this.$route.params.id)
          
        }
      },

      created(){
         // const ref = firebase.firestore().collection('cont_kampus').doc(this.$route.params.id);

          this.ref.get().then((doc) =>{
              
                  this.key = doc.id;
                  this.item = doc.data()
              
          });
          this.ref.collection('cont_jurusan_ahli').onSnapshot((querySnapshot) => {
          this.isi=[];
          querySnapshot.forEach((doc) =>{
            this.isi.push({
              key: doc.id,
              keahlian: doc.data().keahlian,
            })
          })
        })
      }
}
</script>

<style>
.left-text{
  text-align: justify;
}

#about {
  padding: 80px 0px;
  border-bottom: 1px solid #eee;
  padding-top: 9em;
  padding-left: 5em;
  padding-right: 5em;
}

@media (max-width: 765px) {
  #about {
    padding: 50px 0px 80px 0px;
        padding-top:6em;

  }
}

#about .right-text {
  padding-top: 20px;
  align-self: center;
  text-align: left;
}

#about .right-text p {
  margin-bottom: 40px;
}

#about h5
{
  font-size: 26px;
  font-weight: 700;
  color: #1e1e1e;
  line-height: 40px;
  margin-bottom: 25px;
  letter-spacing: 0.5px;
}

#about p {
  margin-bottom: 30px;
  font-size: 16px;
  line-height: 30px;
  letter-spacing: 0.5px;
}

</style>