<template>
    <div>
        <Nav></Nav>

      <section class="section" id="about">
        <div class="container">
            <div class="row">
                <div class="col-lg-7 col-md-12 col-sm-12" data-scroll-reveal="enter left move 30px over 0.6s after 0.4s">
                    <img src="../assets/img/Magang.png" class="rounded img-fluid d-block mx-auto" width="450px" alt="App">
                </div>
                <div class="right-text col-lg-5 col-md-12 col-sm-12 mobile-top-fix" >
                    <div class="left-heading">
                        <h5>Info Jurusan</h5>
                    </div>
                    <div class="left-text">
                        <p>Pelajari lebih lanjut jurusan apa saja yang tersedia di dunia perkuliahan</p>
                    </div>
                </div>                
            </div>
            <div class="row">
                <div class="col-lg-12">
                    <div class="hr"></div>
                </div>
            </div>
        </div>
    </section>

<!-- Page Content -->
<div class="container mb-5 my-5">
  <div class="row">

    <!-- Team Member 1 -->
    <div class="col-xl-3 col-md-6 mb-4" v-for="post in cont_jurusan" v-bind:key="post.key">
      <button @click="Detail(post)" type="button" class="btn btn-primary card border-0 shadow" data-toggle="modal" data-target="#myModal">
        <img :src="post.poster" class="card-img-top" alt="...">
        <div class="card-body text-center">
          <h5 class="card-title mb-2">{{post.jurusan}}</h5>
          <div class="card-text text-black-50">{{post.fakultas}}</div>
        </div>
      </button>
  </div>
  </div>
  </div>

      <Footer></Footer>
    </div>
</template>

<script>
import Nav from './Nav'
import Footer from './Footer'
import firebase from 'firebase'

var Tawk_API=Tawk_API||{}, Tawk_LoadStart=new Date();
(function(){
var s1=document.createElement("script"),s0=document.getElementsByTagName("script")[0];
s1.async=true;
s1.src='https://embed.tawk.to/5dc42b331936a27a715fc5ce/default';
s1.charset='UTF-8';
s1.setAttribute('crossorigin','*');
s0.parentNode.insertBefore(s1,s0);
})();

export default {
    components:{
        Nav,
        Footer,
    },


      data (){
        return {
          cont_jurusan: [],
          ref: firebase.firestore().collection('cont_jurusan')
        }
      },

      created(){
        this.ref.onSnapshot((querySnapshot) => {
          this.cont_jurusan=[];
          querySnapshot.forEach((doc) =>{
            this.cont_jurusan.push({
              key: doc.id,
              jurusan: doc.data().jurusan,
              fakultas: doc.data().fakultas,
              poster: doc.data().poster
            })
          })
        })
      },

      methods: {
        Detail(post){
          this.$router.push({ name:'jurusandetail', params: {id:post.key}})
        }
      }
}
</script>

<style scoped>
.card-title{
  color: black !important;    
}

.modal-header {
  text-align: left;
}

.head {
  background: linear-gradient(to right, #610969, #e30e8e);
  padding: 2em;
}

.card:hover {
    margin-top: -.25rem;
