    box-shadow: 0 0.5rem 1rem 0 rgba(0, 0, 0, 0.3);
    transition: all 0.3s;
  }

header{
  padding: 0em;
}

.btn{
  padding: 0;
}

@import url("https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700,900");




#about {
  padding: 80px 0px;
  border-bottom: 1px solid #eee;
