<template>
    <div>
      <nav class="navbar navbar-expand-lg navbar-dark fixed-top" id="mainNav">
    <div class="container">
      <a class="navbar-brand js-scroll-trigger" href="/">U-City</a>
      <button class="navbar-toggler navbar-toggler-right" type="button" data-toggle="collapse" data-target="#navbarResponsive" aria-controls="navbarResponsive" aria-expanded="false" aria-label="Toggle navigation">
        Menu
        <i class="fas fa-bars"></i>
      </button>
      <div class="collapse navbar-collapse" id="navbarResponsive">
        <ul class="navbar-nav text-uppercase ml-auto">
          <li class="nav-item">
            <a class="nav-link js-scroll-trigger" href="#/about">Tentang</a>
<!--            <router-link to="/about">About</router-link>-->
          </li>
          <li class="nav-item">
            <a class="nav-link js-scroll-trigger" href="#/qna">QnA</a>
          </li>
          <li class="nav-item">
            <a class="nav-link js-scroll-trigger" href="#/pricing">Harga</a>
          </li>
          <li class="nav-item dropdown">
            <a class="nav-link dropdown-toggle" href="#" id="navbarDropdownMenuLink" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            Layanan
            </a>
            <div class="dropdown-menu" aria-labelledby="navbarDropdownMenuLink">
            <a class="dropdown-item" href="#/Tips">Tips & Trik</a>
            <a class="dropdown-item" href="#/Jurusan">Info Jurusan</a>
            <a class="dropdown-item" href="#/Universitas">Info Kampus</a>
            </div>
          </li>
          <li class="nav-item">
              <div v-if="num !== '1'">
              <router-link to="/login" class="btn btn-primary btn-rounded my-2">Masuk</router-link>
              </div>
              <div v-else>
            <a class="nav-link dropdown-toggle" href="#" id="navbarDropdownMenuLink" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            {{ msg }}
            </a>
            <div class="dropdown-menu no" aria-labelledby="navbarDropdownMenuLink">
               <button @click="LogOut" class="btn btn-primary btntext btn-rounded my-2">Log Out</button>
            </div>
              </div>
          </li>
        </ul>
      </div>
    </div>
  </nav>

    </div>
