<script>
import firebase from 'firebase'


export default {
  data() {
    return{
      logemail: '',
      logpassword: '',
      signemail: '',
      signpassword: '',
      msg: 'Log In',
      user:'',
      num: 'loh'
    }
  },

  created(){
    if (firebase.auth().currentUser !== null){
      this.user = firebase.auth().currentUser.email
      this.num = '1'
      this.msg = this.user
    }
  },

  methods: {
    LogIn: function(){
     firebase.auth().signInWithEmailAndPassword(this.logemail, this.logpassword).then(
        function(){
           alert ("Berhasil Login")
           num != null
                   this.$router.replace('/')
        },
        function(err){
          alert (err.message)
        }
      ); 

    },
    SignUp: function(){
      firebase.auth().createUserWithEmailAndPassword(this.signemail, this.signpassword).then(
        function(){
                    alert ('Berhasil Sign Up')

        this.$router.replace('/')
        },
        function(err){
          alert (err.message)
        }
      );  
    },
    LogOut: function(){
      firebase.auth().signOut().then(() => {
        this.$router.replace('/')
      })
    }
  }
}
</script>

